.new-item-form {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.new-item-form label {
  font-size: 2rem;
  text-align: center;
  margin: 1rem;
}
.new-item-form input {
  outline: none;
  border: 1px solid hsl(200, 100%, 40%);
  background: hsl(200, 100%, 30%);
  border-radius: 0.25em;
  padding: 0.5em 0.75em;
  color: hsl(200, 100%, 90%);
}

.new-item-form input:focus {
  border: 1px solid hsl(200, 100%, 70%);
}

.new-item-form input::placeholder {
  color: white;
}
.form-row {
  display: flex;
  flex-direction: column;
  gap: 0.1rem;
}

.todoBtn {
  background: hsl(200, 100%, 50%, 0.1);
  border: 1px solid hsl(200, 100%, 50%);
  color: hsl(200, 100%, 50%);
  padding: 0.5em;
  border-radius: 0.25em;
  cursor: pointer;
  outline: none;
  
}

.todoBtn:hover,
.todoBtn:focus-visible {
  background: hsl(200, 100%, 50%, 0.2);
}

.todoBtn.todoBtn-danger {
  background: hsl(0, 100%, 40%, 0.1);
  border: 1px solid hsl(0, 100%, 40%);
  color: hsl(0, 100%, 40%);
}

.todoBtn.todoBtn-danger:hover,
.todoBtn.todoBtn-danger:focus-visible {
  background: hsl(0, 100%, 40%, 0.2);
}

.todoHeader {
  font-size: 1.5rem;
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
}

.todoList {
  margin: 0;
  padding: 0;
  margin-left: 1rem;
  list-style: none;
}

.todoList li:has(input:checked) label {
  color: hsl(200, 20%, 40%);
}

.todoList {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}

.todoList li {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.todoList li label {
  display: flex;
  gap: 0.25rem;
  cursor: pointer;
  align-items: center;
}

.todoList li:has(input:focus-visible) label {
  outline: 1px solid hsl(200, 100%, 50%);
}

.todoList li input {
  outline: none;
  width: 0;
  height: 0;
  appearance: none;
  pointer-events: none;
  position: absolute;
}

.todoList li label::before {
  content: "";
  display: block;
  width: 0.9rem;
  height: 0.9rem;
  background: hsl(200, 100%, 90%);
  border-radius: 0.25em;
  display: flex;
  justify-content: center;
  align-items: center;
}

.todoList li label:hover::before {
  background: hsl(200, 100%, 80%);
}

.todoList li:has(input:checked) label::before {
  content: "âœ”";
  background: hsl(200, 100%, 40%);
  color: hsl(200, 100%, 90%);
  font-size: 0.75rem;
  font-weight: bold;
}

.todoList li:has(input:checked) label:hover::before {
  background: hsl(200, 100%, 30%);
}